import React from "react";
import { useAppContext } from "../context/AppContext";

function ServerSelector() {
  const { serverUrl, updateServer } = useAppContext();

  const serverOptions = [
    { label: "Albion Online Americas Server", value: "https://west.albion-online-data.com" },
    { label: "Albion Online Asia Server", value: "https://east.albion-online-data.com" },
    { label: "Albion Online Europe Server", value: "https://europe.albion-online-data.com" },
  ];

  return (
    <select
      value={serverUrl}
      onChange={(e) => updateServer(e.target.value)}
      className="server-dropdown"
    >
      {serverOptions.map((option) => (
        <option key={option.value} value={option.value}>
          {option.label}
        </option>
      ))}
    </select>
  );
}

export default ServerSelector;
