import React, { useState, useEffect } from "react";
import Select from "react-select";
import { parseXML } from "../utils/xmlParser";
import { useAppContext } from "../context/AppContext";
import "../styles/MarketPriceSpinner.css"; // For spinner styles

function MarketPriceChecker() {
  const { serverUrl } = useAppContext(); // Use the selected server
  const [shopCategories, setShopCategories] = useState({});
  const [items, setItems] = useState({});
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [selectedSubcategory, setSelectedSubcategory] = useState(null);
  const [selectedItem, setSelectedItem] = useState(null);
  const [itemDetails, setItemDetails] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        // Check if data is cached
        const cachedData = localStorage.getItem("albionData");
        if (cachedData) {
          const { shopCategories, items } = JSON.parse(cachedData);
          setShopCategories(shopCategories);
          setItems(items);
        } else {
          // Fetch XML and parse data
          const xmlData = await fetch(
            "https://raw.githubusercontent.com/ao-data/ao-bin-dumps/refs/heads/master/items.xml"
          ).then((res) => res.text());

          const { shopCategories, items } = parseXML(xmlData);

          // Cache the parsed data
          localStorage.setItem(
            "albionData",
            JSON.stringify({ shopCategories, items })
          );

          setShopCategories(shopCategories);
          setItems(items);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  const categoryOptions = Object.keys(shopCategories).map((key) => ({
    id: shopCategories[key].id,
    value: shopCategories[key].value,
    label: shopCategories[key].id,
  }));

  const subcategoryOptions =
    selectedCategory &&
    Object.keys(shopCategories[selectedCategory.id]?.subcategories || {}).map(
      (key) => ({
        id: shopCategories[selectedCategory.id].subcategories[key].id,
        value: shopCategories[selectedCategory.id].subcategories[key].value,
        label: shopCategories[selectedCategory.id].subcategories[key].id,
      })
    );

  const itemOptions =
    selectedCategory &&
    selectedSubcategory &&
    Object.values(items)
      .filter(
        (item) =>
          item.shopcategory === selectedCategory.id &&
          item.shopsubcategory1 === selectedSubcategory.id
      )
      .map((item) => ({
        value: item.uniquename,
        label: item.uniquename,
      }));

  const handleItemSelect = (item) => {
    setSelectedItem(item);
    setItemDetails(items[item.value]);
  };

  return (
    <div>
      <h2>Market Price Checker</h2>
      {isLoading ? (
        <div className="spinner-container">
          <div className="spinner"></div>
        </div>
      ) : (
        <div>
          <Select
            options={categoryOptions}
            onChange={(category) => {
              setSelectedCategory(category);
              setSelectedSubcategory(null);
              setSelectedItem(null);
              setItemDetails(null);
            }}
            placeholder="Select Category"
            isDisabled={isLoading}
          />
          <Select
            options={subcategoryOptions}
            onChange={(subcategory) => {
              setSelectedSubcategory(subcategory);
              setSelectedItem(null);
              setItemDetails(null);
            }}
            placeholder="Select Subcategory"
            isDisabled={!selectedCategory || isLoading}
          />
          <Select
            options={itemOptions}
            onChange={handleItemSelect}
            placeholder="Select Item"
            isDisabled={!selectedSubcategory || isLoading}
          />
        </div>
      )}

      {itemDetails && (
        <div className="item-details">
          <h3>Item Details</h3>
          <p>
            <strong>Unique Name:</strong> {itemDetails.uniquename}
          </p>
          <p>
            <strong>Category:</strong> {itemDetails.shopcategory}
          </p>
          <p>
            <strong>Subcategory:</strong> {itemDetails.shopsubcategory1}
          </p>
        </div>
      )}
    </div>
  );
}

export default MarketPriceChecker;
