


import { XMLParser } from 'fast-xml-parser';

export function parseXML(xmlData) {
    const parser = new XMLParser({
        ignoreAttributes: false,
        attributeNamePrefix: '',
    });
    const jsonObj = parser.parse(xmlData);

    const shopCategoriesMap = {};
    const itemsMap = {};

    // Process shopcategories
    const shopcategories = jsonObj.items.shopcategories.shopcategory || [];
    shopcategories.forEach((item) => {
        const shopCategoryId = item.id;

        if (!shopCategoriesMap[shopCategoryId]) {
            shopCategoriesMap[shopCategoryId] = {
                id: item.id,
                value: item.value,
                subcategories: item.shopsubcategory || [],
            };
        }
    });

    // Process other items
    Object.keys(jsonObj.items).forEach((key) => {
        if (key === 'shopcategories') return; // Skip shopcategories

        const itemThing = jsonObj.items[key];
        processItems(itemsMap, itemThing)
    });

    return {
        shopCategories: shopCategoriesMap,
        items: itemsMap,
    };
}

function processItems(itemsMap, item) {
    if (Array.isArray(item)) {
      // If data is an array
      item.forEach((x) => {
        // Process each item in the array
        processItems(itemsMap, x)
      });
    } else if (typeof item === 'object' && item !== null) {
      // If data is a JSON object
      
        const { uniquename, shopcategory, shopsubcategory1, craftingrequirements } = item;

        if (uniquename) {
            itemsMap[uniquename] = {
                uniquename,
                shopcategory,
                shopsubcategory1,
            };

            // Add crafting requirements if they exist
            if (craftingrequirements) {
                // itemsMap[uniquename].craftingrequirements = {
                //     silver: craftingrequirements.silver || 0,
                //     craftresource: (craftingrequirements.craftresource || []).map((resource) => ({
                //         count: resource.count,
                //         uniquename: resource.uniquename,
                //     })),
                // };
                mapRequirements(itemsMap,craftingrequirements, uniquename)
            }
        }
    } else {
      // Handle other data types (e.g., string, number)
      console.log("Data is not an array or JSON object.");
      console.log(item)
    }
  }

  function mapRequirements(itemsMap,craftingRequirements, uniquename){
    if (craftingRequirements.craftresource){
        if (Array.isArray(craftingRequirements.craftresource)){
            itemsMap[uniquename].craftingrequirements = {
                silver: craftingRequirements.silver || 0,
                craftresource: (craftingRequirements.craftresource || []).map((resource) => ({
                    count: resource.count,
                    uniquename: resource.uniquename,
                })),
            };
        }else{
            itemsMap[uniquename] = [{count: craftingRequirements.craftresource.count, uniquename:craftingRequirements.craftresource.uniquename}]
        }
    }else{
        itemsMap[uniquename].craftingrequirements = craftingRequirements
    }
  }