import React, { useState } from "react";
import Select from "react-select";
import { fetchPrices } from "../utils/api";

function FarmPage() {
  const [selectedItem, setSelectedItem] = useState(null);
  const [marketPrices, setMarketPrices] = useState(null);
  const [npcPrice, setNpcPrice] = useState(null);

  const items = []; // Populate from file

  const handleFetchPrices = async () => {
    const region = "YourRegion"; // Replace with region logic
    const marketData = await fetchPrices(region, selectedItem, []);

    setMarketPrices(marketData[0]); // Assume lowest price
  };

  return (
    <div>
      <h2>Farm Page</h2>
      <Select
        options={items}
        onChange={setSelectedItem}
        placeholder="Select Item"
      />
      <button onClick={handleFetchPrices}>Fetch Prices</button>
      {npcPrice && <p>NPC Price: {npcPrice.price}</p>}
      {marketPrices && <p>Market Price: {marketPrices.price}</p>}
    </div>
  );
}

export default FarmPage;
