import axios from 'axios';

const api = axios.create();

export function fetchPrices(region, itemName, cities) {
    const url = ''; // Add your API endpoint here
    return api.post(url, { region, itemName, cities });
}


export function fetchNpcPrices(region, itemName) {
    const url = ''; // Add your API endpoint here
    return api.get(`${url}/${region}/${itemName}`);
}
