import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  NavLink,
} from "react-router-dom";
import MarketPriceChecker from "./components/MarketPriceChecker";
import ProfitCalculator from "./components/ProfitCalculator";
import FarmPage from "./components/FarmPage";
import ServerSelector from "./components/ServerSelector";
import "./styles/App.css";
import { AppProvider } from './context/AppContext';

function App() {
  return (
    <AppProvider>
    <Router basename="/albion-calculator">
      <div className="app-container">
        <nav className="navbar">
          <NavLink to="/" className="nav-link" activeclassname="active-link">
            Market Price Checker
          </NavLink>
          <NavLink
            to="/profit-calculator"
            className="nav-link"
            activeclassname="active-link"
          >
            Profit Calculator
          </NavLink>
          <NavLink
            to="/farm-page"
            className="nav-link"
            activeclassname="active-link"
          >
            Farm Page
          </NavLink>
          {/* Server Selector */}
          <div className="server-selector">
            <ServerSelector />
          </div>
        </nav>
        <main className="content">
          <Routes>
            <Route path="/" element={<MarketPriceChecker />} />
            <Route path="/profit-calculator" element={<ProfitCalculator />} />
            <Route path="/farm-page" element={<FarmPage />} />
          </Routes>
        </main>
      </div>
    </Router>
    </AppProvider>
  );
}

export default App;
